<template>
  <div>
    <fTitle>
      <template #title>Loading 加载</template>
      <template #introduce>加载数据时显示动效。</template>
    </fTitle>
    <sTitle>
      <template #title>loading的五种方式</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="24">
<div class="grid-content">
  <el-button-group>
  <el-button type="primary" :disabled="loading1" @click="handleLoading1">区域加载默认</el-button>
  <el-button type="primary" :disabled="loading2" @click="handleLoading2">区域加载自定义</el-button>
  <el-button type="primary" :disabled="loading3" @click="handleLoading3" v-loading.fullscreen.lock="loading3">整页指令方式</el-button>
  <el-button type="primary" :disabled="loading4" @click="handleLoading4">整页服务方式</el-button>
  <el-button type="primary" :disabled="loading5" @click="handleLoading5">引入服务方式</el-button>
</el-button-group>
</div>
    </el-col></el-row>
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-table
    v-loading="loading1"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="date"
      label="日期"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址">
    </el-table-column>
  </el-table>
</div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-table
    v-loading="loading2"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="date"
      label="日期"
      width="180">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址">
    </el-table-column>
  </el-table>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
import { Loading } from 'element-ui'
export default {
  name: 'ColorIndex',
  data () {
    return {
      loading1: false,
      loading2: false,
      loading3: false,
      loading4: false,
      loading5: false,
      tableData: [{
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-04',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }]
    }
  },
  methods: {
    handleLoading1 () {
      this.loading1 = true
      const time = setTimeout(() => {
        this.loading1 = false
        clearTimeout(time)
      }, 2000)
    },
    handleLoading2 () {
      this.loading2 = true
      const time = setTimeout(() => {
        this.loading2 = false
        clearTimeout(time)
      }, 2000)
    },
    handleLoading3 () {
      this.loading3 = true
      const time = setTimeout(() => {
        this.loading3 = false
        clearTimeout(time)
      }, 2000)
    },
    handleLoading4 () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.loading4 = true
      const time = setTimeout(() => {
        loading.close()
        this.loading4 = false
        clearTimeout(time)
      }, 2000)
    },
    handleLoading5 () {
      this.loading5 = true
      const loadingInstance = Loading.service({ fullscreen: true })
      const time = setTimeout(() => {
        this.loading5 = false
        this.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
          loadingInstance.close()
        })
        clearTimeout(time)
      }, 2000)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
      /deep/ .el-button--small{
      font-size: 16px;
      }}
    }}
</style>
